/* sample file */
body {
  margin: 0;
  padding: 0;
  border: none;
  font-size: 14px;
  letter-spacing: 0;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  font-family: 'Catamaran', sans-serif;
  color: #fff;
  background-image: url(/assets/img/bg.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-position: center -1px;
  background-color: #151515;
}

body * {
  transition: all .3s ease;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  &:hover {
    text-decoration: none;
  }
}

@media (max-width: 767px) {
  .hidden-xs {
    display: none !important;
  }
}

@media (max-width: 991px) {
  .hidden-sm {
    display: none !important;
  }
}

@media (max-width: 1199px) {
  .hidden-md {
    display: none !important;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}


.profile {
  display: flex;
  flex-direction: column;
  align-items: center;

  .image {
    position: relative;
    width: 100px;
    height: 100px;
    margin-top: 40px;
    margin-bottom: 30px;

    img {
      width: 95px;
      height: 95px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      z-index: 2;
    }

    &:before {
      content: "";
      width: 100px;
      height: 100px;
      position: absolute;
      z-index: 1;
      transition: box-shadow .4s ease;
      border-radius: 100px;
      background-image: linear-gradient(138deg, #00a4ff, #77bbfa 13%, #77bbfa 43%, #00a4ff 55%, #7e19ba);
    }
  }
}

.logo-wrapper {
  max-width: 250px;
  margin: 0 auto;
  margin-bottom: 22px;

  .logo {
    width: 100%;
  }
}

.title {
  font-size: 23px;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.13;
  letter-spacing: 1.7px;
  text-align: center;
  color: #006bff;
  margin-bottom: 25px;
}

.subtitle {
  font-size: 14px;
  font-weight: 100;
  line-height: normal;
  margin-bottom: 10px;
  text-align: center;
  letter-spacing: 0.65px;

  span {
    font-weight: normal;
  }
}

.contract {
  font-size: 14px;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 2.7px;
  text-align: center;
  display: block;
  color: #ffffff;

  &:hover {
    color: #f7f7f7;
  }
}

.content {
  font-size: 17px;
  font-weight: 300;
  line-height: normal;
  color: #f7f7f7;
  letter-spacing: 0.7px;
}

.social {
  margin-top: 60px;
  margin-bottom: 60px;

  ul {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    li {
      &+li {
        margin-left: 34px;
      }

      a {
        width: 44px;
        height: 44px;
        border-radius: 44px;
        background-color: #4c4c4c;
        display: inline-block;
        text-align: center;
        line-height: 44px;
      }

      .text {
        display: none;
      }
    }
  }

}

.dot-pulse {
  position: relative;

  &:after {
    content: "";
    width: 6px;
    height: 6px;
    margin-left: 5px;
    border-radius: 6px;
    display: inline-block;
    box-shadow: 0 0 8px 2px rgba(0, 107, 255, 1);
    background-image: linear-gradient(to right, #00a4ff, #77bbfa 13%, #77bbfa 43%, #00a4ff);
    animation: pulse 2s infinite;
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 8px 0px rgba(0, 107, 255, 0.6);
  }

  70% {
    box-shadow: 0 0 4px 6px rgba(0, 107, 255, 0.3);
  }

  100% {
    box-shadow: 0 0 0px 0px rgba(0, 107, 255, 0.1);
  }
}

.pathlogo1,
.pathlogo2 {
  opacity: 0;
  transition: opacity 1s ease;

}

@keyframes rotate {
  from {
    transform: rotate(0deg)
  }

  to {
    transform: rotate(360deg)
  }
}

.clients-list {
  position: relative;
  margin-top: 30px;
  margin-bottom: 60px;

  ul {
    display: flex;
    align-items: center;
    flex-direction: column;

    li {
      text-align: center;
      width: 100px;
      padding-bottom: 30px;

      &+li {
        padding-top: 30px;
        border-top: 2px solid #333333;
      }
    }
  }
}

.show-effects {

  .card-wrapper {
    .profile {
      .image {
        &:before {
          content: "";
          animation: rotate 3s infinite;
          animation-timing-function: cubic-bezier(1, 0, .5, 1);
          box-shadow: 0 0 11px 0 #006bff;
        }
      }
    }
  }

  .pathlogo1,
  .pathlogo2 {
    opacity: 1;
  }
}



@media (max-width: 767px) {
  .hidden-xs {
    display: none;
  }
}

@media (min-width: 992px) {
  .wrapper {
    position: absolute;
    overflow: hidden;
    width: 100%;
    height: 100%;

    .container {
      position: relative;
      height: 100%;

      &:before,
      &:after {
        position: absolute;
        content: "";
        left: 79px;
        width: 58px;
        max-width: 58px;
        z-index: -1;
        background-repeat: no-repeat;
        background-size: 58px 100%;
        background-position-x: center;
      }

      &:before {
        top: 0;
        height: 11%;
        background-image: url(/assets/img/strip-top.png);
      }

      &:after {
        bottom: 0;
        height: 30%;
        background-image: url(/assets/img/strip-bottom.png);
      }

    }
  }

  .card-wrapper {
    position: absolute;
    top: 10%;
    left: 0;
    width: 100%;
    height: 50%;
    padding: 60px 0;
  }

  .profile {
    flex-direction: row;
    align-items: flex-start;
    margin-bottom: 40px;

    .image {
      position: relative;
      width: 164px;
      height: 164px;
      margin: 0 120px 0 25px;

      img {
        width: 156px;
        height: 156px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        z-index: 2;
      }

      &:before {
        content: "";
        width: 164px;
        height: 164px;
        position: absolute;
        z-index: 1;
        transition: box-shadow .4s ease;
        border-radius: 164px;
        background-image: linear-gradient(138deg, #00a4ff, #77bbfa 13%, #77bbfa 43%, #00a4ff 55%, #7e19ba);
      }
    }
  }

  .logo-wrapper {
    max-width: 370px;
    margin: 0;
    margin-bottom: 23px;
  }

  .title {
    font-size: 32px;
    line-height: 1.22;
    letter-spacing: 7px;
    color: #216fdb;
    margin-bottom: 10px;
  }

  .subtitle {
    text-align: left;
    letter-spacing: 0.7px;
  }

  .contract {
    letter-spacing: 1.4px;
    text-align: left;
  }

  .social {
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 96px;

    ul {
      flex-direction: column;
      align-items: flex-start;

      li {
        margin-bottom: 30px;
        position: relative;

        &+li {
          margin-left: 0;
        }

        a {
          width: auto;
          height: auto;
          background-color: transparent;
          line-height: normal;
        }

        [class^="icon"] {
          svg {
            transition: filter .4s ease;
            filter: drop-shadow(0px 0px 7px rgba(255, 255, 255, 0));
          }
        }

        .text {
          font-size: 14px;
          font-weight: 100;
          position: relative;
          line-height: normal;
          letter-spacing: 0.6px;
          color: #ffffff;
          margin-left: 30px;
          display: inline-block;
          opacity: 0;
          transition: opacity .4s ease;
          visibility: hidden;

          &:after {
            content: "";
            background-image: linear-gradient(to right, #00a4ff, #77bbfa);
            width: 0;
            transition: width .2s linear;
            height: 2px;
            border-radius: 1px;
            box-shadow: 0 0 14px 0 #006bff;
            position: absolute;
            bottom: -2px;
            left: 0;
          }
        }

        &:hover {
          [class^="icon"] {
            svg {
              filter: drop-shadow(0px 0px 7px rgba(255, 255, 255, 0.5));
            }
          }

          .text {
            opacity: 1;
            visibility: visible;

            &:after {
              width: 31px;
            }
          }
        }
      }
    }
  }

  .clients-list {
    width: calc(100% - 200px);
    position: relative;
    left: 200px;
    margin-top: 230px;
    margin-bottom: 0;

    ul {
      flex-direction: row;

      li {
        height: 86px;
        line-height: 86px;
        flex: 0 0 20%;
        padding: 0 60px;
        text-align: center;
        padding-top: 0 !important;

        &+li {
          border-top: 0;
          border-left: 1px solid #4c4c4c;
        }
      }
    }
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
  .social {
    margin-left: 312px;

    ul {
      flex-direction: row;
      justify-content: flex-start;

      li {
        &+li {
          margin-left: 30px;
        }

        .text {
          display: none;
        }
      }
    }
  }

  .content {
    margin-left: 312px;
  }

  .clients-list {
    left: 264px;
    width: calc(100% - 264px);

    ul {
      li {
        padding: 0 20px;
      }
    }
  }
}

@media (min-width: 1200px) and (max-height: 1023px) {
  .clients-list {
    margin-top: 120px;
  }
}

@media (max-width: 1199px) and (max-height: 1023px) {
  .wrapper {
    overflow: auto;
  }

  .container {
    &:after {
      height: 50%;
    }
  }

  .clients-list {
    margin-top: 60px;
  }
}

@media (max-height: 799px) {
  .wrapper {
    overflow: auto;
  }

  .card-wrapper {
    top: 0;
    padding: 30px 0;
  }

  .clients-list {
    margin-top: 0;
  }
}


@media (min-height: 800px) {
  .wrapper {
    overflow: auto;

    .container {

      &:after {
        height: 25%;
      }
    }
  }
}

@media (min-height: 1100px) {
  .wrapper {
    .container {
      &:after {
        height: 35%;
      }
    }
  }
}

@media (min-height: 1250px) {
  .wrapper {
    .container {

      &:after {
        height: 40%;
      }
    }
  }
}